
import { defineComponent } from "vue";

export default defineComponent({
  name: "App",
  data() {
    return {
      activeId: -1,
      selectedId: -1,
      tasks: [],
    };
  },
  mounted() {
    fetch("/api/list", {
      method: "GET",
    })
      .then((data) => {
        data.json().then((text) => {
          this.tasks = text.tasks[0];
        });
      })
      .catch((e) => {
        console.error(e);
      });

    setInterval(() => {
      fetch("/api/get", {
        method: "GET",
      })
        .then((data) => {
          data.json().then((text) => {
            this.activeId = text.id;
          });
        })
        .catch((e) => {
          console.error(e);
        });
    }, 500);
  },
  methods: {
    play() {
      fetch(`/api/set/${this.selectedId}`, {
        method: "GET",
      });
    },
  },
});
